@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root{margin:0;padding:0;height:100%}body{width:100%;height:100%}#root{width:100%;height:100%;background-color:#8ad6cf}.navbar{display:flex;justify-content:space-between;align-items:center;padding:10px 50px;height:80px;background-color:#284b70}.navbar h1{font-family:"Dancing Script",cursive;font-weight:bold;color:#00b1b0;font-size:2.5rem;margin:0}.navbar .nav-links{width:300px;display:flex;justify-content:space-between;padding-left:0}.navbar .nav-links a{font-style:italic;text-decoration:none;font-size:1.1rem;color:#fec84d}.navbar .nav-links .active-link{font-style:normal;font-weight:bold;color:#00b1b0}.container{width:90%;margin:50px auto;color:#432f70}.title{margin:0;font-size:2rem}.container-calc{display:flex;justify-content:space-between}.home-details,.quote{word-spacing:5px;font-size:1.3rem;margin-top:70px}.quote{margin-top:100px}

.calculator{display:flex;flex-flow:column;width:60%;height:500px;margin-top:100px}.calculator .output{margin:0;display:flex;justify-content:flex-end;height:70px;background-color:#284b70;overflow-x:auto}.calculator .output .screen{margin:0 5px 0 0;padding-top:10px;text-align:center;vertical-align:middle;color:white;font-size:2rem;overflow-x:auto}.calculator .buttons-container{display:grid;grid-template-columns:repeat(4, 1fr);height:70%}.calculator .buttons-container .button{border:1px solid #a3a5c7;font-size:1rem}.calculator .buttons-container .button:active{background-color:#a3a5c7}.calculator .buttons-container .operator{background-color:#fec84d}.calculator .buttons-container #zero{-ms-grid-column:1/2;grid-column:1/3}

